.card {
  width: 200px;
  padding-top: 32px;
  padding-bottom: 16px;
  display: flex;
  flex-direction: column;
  flex-shrink: 0;
  border-radius: 5px;
  border: 1px solid #f3f3f3;
  background: $white;
  transition: all 0.3s ease;

  &:hover {
    transform: scale(1.2);
  }

  &__product {
    &-img {
      margin: 0 auto;
      width: 160px;
      height: 134px;
    }

    &-name {
      margin: 40px 16px 4px;
      color: $main-accent;
      font-size: 12px;
      font-weight: 500;
      line-height: 18px;
    }
  }

  &__product-code {
    margin: 0 16px 16px;
    min-height: 14px;
    color: $secondary-accent;
    font-size: 10px;
    font-weight: 400;
  }

  &__product-price {
    margin: 24px 16px 0;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  &:hover .card__product-name {
    color: #34568b;
  }

  &__product-priceText {
    color: $secondary-accent;
    font-size: 12px;
    line-height: 18px;
  }

  &__product-priceValue {
    color: $main-accent;
    font-size: 16px;
    font-weight: 700;
    line-height: 18px;
  }

  &__purchase-link {
    margin: 16px 16px 0;
    padding: 11px 0;
    line-height: 16px;
    text-decoration: none;
    font-size: 14px;
    font-style: normal;
    text-transform: uppercase;
    color: $white;
    background: $blue-accent;
    flex-shrink: 0;
    border: 1px solid transparent;
    border-radius: 5px;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: background-color 0.3s ease,
      color 0.3s ease,
      border-color 0.3s ease;

    &:hover {
      background-color: $white;
      color: $blue-accent;
      border: 1px solid $blue-accent;
      border-radius: 5px;
    }
  }
}
